/* Container styling */
.container2 {
  width: 100%;
  max-width: 800px;
  margin: 50px auto;
  padding: 20px;
  background-color: #f9f9f9;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

.header {
  display: flex;
  justify-content: center;
  font-size: 32px;
  margin: 10px 150px;
  color: #ffb32c;
  background-color: #272727;
  border: 1px solid #272727;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}

/* Form title styling */
h2 {
  text-align: center;
  color: #333;
  margin-bottom: 20px;
}

/* Form styling */
.form {
  display: flex;
  flex-direction: column;
}

.form-div {
  margin-bottom: 15px;
}

/* Label styling */
.label {
  font-weight: bold;
  margin-bottom: 5px;
  display: block;
  color: #555;
}

/* Input styling */
.input {
  width: 100%;
  padding: 10px;
  font-size: 16px;
  border: 1px solid #ddd;
  border-radius: 4px;
  box-sizing: border-box; /* Ensures padding doesn't affect width */
  background-color: #fff;
}

.input:focus {
  border-color: #45a049;
  outline: none;
}

/* Button styling */
.btn2 {
  padding: 10px 20px;
  background-color: #45a049;
  color: white;
  border: none;
  border-radius: 4px;
  font-size: 16px;
  cursor: pointer;
  transition: background-color 0.3s ease;
  align-self: center; /* Center the button */
}

.btn2:hover {
  background-color: #285e2b;
}

/* Responsive adjustments */
@media screen and (max-width: 480px) {
  .container {
    padding: 15px;
  }

  .label {
    font-size: 14px;
  }

  .input {
    font-size: 14px;
  }

  .btn {
    width: 100%; /* Make the button full-width on small screens */
    font-size: 14px;
  }
}
